.friendMessage {
    display: flex;
    flex-direction: column;
    width: fit-content;
}

.friendImageAndText {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    flex-shrink: 0;
    border-radius: 48px;
    background:  lightgray -16.029px 0 / 150.121% 100% no-repeat;
}

.friendText {

}

.friendText:after {

}

.friendName {
    color: #000;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.friendMessageText {
    color: #000;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.friendTime {
    color: #000;
    font-family: Montserrat;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-left: 12px;
}
