.message {
    display: flex;
    flex-direction: column;
    width: fit-content;
}

.imageAndText {
    display: flex;
    justify-content: end;
    align-items: center;

}

.imageAndText img {
    width: 48px;
    height: 48px;
    flex-shrink: 0;
    border-radius: 48px;
    background: lightgray -16.029px 0px / 150.121% 100% no-repeat;
}

.text {

}

.text:after {

}

.name {
    color: #a61414;
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.messageText {
    color: #ec0505;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.time {
    color: #000;
    font-family: Montserrat;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-left: 12px;
}