.sendForm {

    display: flex;
    align-items: center;
    margin: 0 70px 0 70px;
}

.textarea {
resize: none;
    width: 976px;
    max-width: 100%;
    height: 40px !important;
padding: 10px 0;
    border-radius: 20px;
    background: #F5F7FB;
    box-shadow: 0 5px 20px 0 rgba(29, 33, 38, 0.03), 0 1px 2px 0 rgba(29, 33, 38, 0.10);
    border: none;

}

.button {
    margin-left: 44px;
    width: 120px;
    height: 40px;
    flex-shrink: 0;
    border-radius: 20px;
    background: #06C;
    color: #FFF;
    font-family: Montserrat sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    border: none
}

